import { BlockNumber } from '../../core'
import { Token } from '../../tokens'
import { Web3CallData } from '../../web3-client'
import { ensureAbi } from '../libs/vault-web3.lib'
import {
  Vault,
  VaultContractData,
  VaultContractModel,
  VaultContractOptions,
  VaultNonPayableMethodOptions,
  VaultNonPayableMethodType,
  VaultPayableMethodType,
} from '../vault.model'
import { VaultContract } from './vault-contract.class'

export class VaultCDO extends VaultContract implements VaultContractModel {
  constructor(vault: Vault, token: Token, options?: VaultContractOptions) {
    super(vault, token, options)

    if (!vault.cdo) {
      throw new Error('Vault without CDO data')
    }
  }

  /**
   * Get contract data
   * @returns the blockchain contract data
   */
  public async getContractData(
    blockNumber: BlockNumber = 'latest'
  ): Promise<VaultContractData> {
    return this.getData(this.makeCallData(), blockNumber)
  }

  /**
   * Get vault non payable method
   * @param type
   * @param params
   */
  public getValue(
    type: VaultNonPayableMethodType,
    options?: VaultNonPayableMethodOptions
  ): Promise<any> {
    switch (type) {
      default:
        throw new Error('Method not available for this kind of vault')
    }
  }

  /**
   * Prepare call data
   * @returns the web3 call data
   */
  private makeCallData(): Web3CallData[] {
    // Parse vault contract methods
    const { abi: vaultAbi, abiCode, address, protocol } = this.vault
    const abi = ensureAbi({ abi: vaultAbi, abiCode })
    let callData = this.makeProtocolData({ abi, address, protocol }, 'TRANCHE')

    // TODO: this should not be necessary. Fix VaultCdoType
    if (!this.vault.cdo) {
      return []
    }

    // Parse vault CDO methods
    callData = [
      ...callData,
      ...this.makeProtocolData(
        {
          ...this.vault.cdo,
          protocol: this.vault.protocol,
        },
        'CDO'
      ),
    ]

    if (this.vault.strategy) {
      callData = [
        ...callData,
        ...this.makeProtocolData(
          {
            ...this.vault.strategy,
            protocol: this.vault.protocol,
          },
          'STRATEGY'
        ),
      ]
    }

    // Parse wallet methods
    if (this.walletAddresses) {
      callData = this.walletAddresses.reduce(
        (acc, walletAddress) => [
          ...acc,
          ...this.makeWalletData(walletAddress, { abi, address, protocol }),
        ],
        callData
      )
    }

    // Parse vault pools methods
    if (this.vault.pools) {
      callData = this.vault.pools.reduce(
        (acc, pool) => [...acc, ...this.makePoolData(pool)],
        callData
      )
    }

    // Parse token methods
    if (this.token.oracle) {
      callData = [
        ...callData,
        ...this.makeProtocolData(this.token.oracle, 'ORACLE', this.token),
      ]
    }

    return callData
  }
}
